umento de carácter técnico elaborado por el Instituto Colombiano de Normas Técnicas y Certificación (ICONTEC) y cuyo objetivo es la salvaguardia de las personas y de los bienes contra los riesgos que pueden surgir por el uso de la electricidad y que no pretende marcar especificaciones de diseño ni de ser un manual de instrucciones para personal no calificado. Antes de la entrada en vigencia del RETIE, era el marco normativo de referencia para los organismos con jurisdicción legal sobre las instalaciones eléctricas. Cuando el Gobierno Nacional permitió a las empresas de derecho privado acceder a la propiedad de infraestructura del sector eléctrico, se eliminó la obligatoriedad de cumplimiento de las normas técnicas y se adoptó el concepto de reglamento técnico, donde por definición no se pueden crear obstáculos innecesarios al comercio y cualquier restricción debe referirse sólo a objetivos legítimos como la protección de la vida y salud humana, de la vida animal y vegetal y del medio ambiente. Sin embargo, como ya se mencionó, el RETIE en el numeral 27.1 establece el contenido de los capítulos uno al siete y el del diez de la NTC 2050, primera actualización (1998), como de obligatorio cumplimiento.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Está organizado en capítulos y artículos así:")]),_c('SlyderB',{staticClass:"mb-5",attrs:{"datos":_vm.datosSlyder3}}),_vm._m(11),_c('p',{staticClass:"mb-5"},[_vm._v("Está organizado en títulos y capítulos así:")]),_c('SlyderB',{staticClass:"mb-4",attrs:{"datos":_vm.datosSlyder4}}),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-5"}),_c('div',{staticClass:"col-lg-7"},[_c('a',{staticClass:"anexo mb-4 mb-lg-0",attrs:{"href":_vm.obtenerLink('downloads/Resolucion_5018_del_20112019_SST_en_energia_electrica.pdf'),"target":"_blank"}},[_vm._m(12),_vm._m(13)])])])],1)],1)}
